/**
 * CONTACT FORM
 */

 /**
  * Dependencies.
  */
import React, { useState, useContext } from "react"
import { useForm } from "react-hook-form"
//import styled from 'styled-components'
import { LocalesContext } from '../components/locales/LocalesContextProvider'

const FAKE_GATEWAY_URL = "https://jsonplaceholder.typicode.com/posts"


/**
 * Component (JSX).
 */
const ContactForm = () => {

  /**
   * I18N.
   */
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const contactForm = i18nData.locales['contact-form']
  const required = `${contactForm.Required}`

  const [submitted, setSubmitted] = useState(false)
  const {
    register,
    handleSubmit,
    setError,
    errors,
    reset,
    formState: { isSubmitting }
  } = useForm()

  const onSubmit = async data => {
    try {
      await fetch(FAKE_GATEWAY_URL, {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        body: JSON.stringify(data),
        headers: {
          "Content-type": "application/json; charset=UTF-8"
        }
      })
      setSubmitted(true)
      reset()
    } catch (error) {
      setError(
        "submit",
        "submitError",
        `${contactForm.Error} ${error.message}`
      )
    }
  }

  const showSubmitError = msg => <p className="msg-error">{msg}</p>;

  const showThankYou = (
    <div className="msg-confirm">
      <p>{contactForm.Success}</p>
      <button type="button" onClick={() => setSubmitted(false)}>
      {contactForm.Reset}
      </button>
    </div>
  )

  const showForm = (
    <form onSubmit={handleSubmit(onSubmit)} method="post">
      <label htmlFor="name">{contactForm.Name}</label>
      <input
        type="text"
        name="name"
        id="name"
        placeholder={contactForm.NamePlaceholder}
        ref={register({ required })}
        disabled={isSubmitting}
      />
      {errors.name && <div className="msg-error">{errors.name.message}</div>}

      <label htmlFor="email">{contactForm.Email}</label>
      <input
        type="email"
        name="email"
        id="email"
        placeholder={contactForm.EmailPlaceholder}
        ref={register({ required })}
        disabled={isSubmitting}
      />
      {errors.email && (
        <div className="msg-error">{errors.email.message}</div>
      )}

      <label htmlFor="question">{contactForm.Message}</label>
      <textarea
        ref={register({ required })}
        name="question"
        id="question"
        rows="3"
        placeholder={contactForm.MessagePlaceholder}
        disabled={isSubmitting}
      />
      {errors.question && (
        <div className="msg-error">{errors.question.message}</div>
      )}

      <button type="submit" disabled={isSubmitting}>{contactForm.Send}</button>
    </form>
  )

  return (
    <div className="page contact-page">
      <div className="text-side">
        {errors && errors.submit && showSubmitError(errors.submit.message)}
      </div>
      <div className="form-side">{submitted ? showThankYou : showForm}</div>
    </div>
  )

}

export default ContactForm
